// 
const changeColorTheme = function (id, href) {
    var dom = document.getElementById(id);
    dom.href = href
}
// ,
const isHasInParent = function (parentId, dom) {
    // DOM
    if (!(dom instanceof HTMLElement)) {
        return false;
    }
    // 
    if (parentId == dom.id) {
        return true;
    }
    // body,,
    if ('BODY' !== dom.parentElement.nodeName) {
        if (parentId == dom.parentElement.id) {
            return true;
        }
        // 
        return isHasInParent(parentId, dom.parentElement)
    } else {
        return false;
    }
}
// 
const getAllParents = function (dom, parentTagList = []) {
    // DOM
    if (!(dom instanceof HTMLElement)) {
        return console.error('receive only HTMLElement');
    }
    // body,,
    if ('BODY' !== dom.parentElement.nodeName) {
        // 
        parentTagList.push(dom.parentElement)
        // 
        return getParentTag(dom.parentElement, parentTagList)
    } else {
        return parentTagList;
    }
}

/**
 * : 
 * : prop 
 */
HTMLElement.prototype.getStyle = function (prop) {
    if (!prop) {
        return window.getComputedStyle(this, null) || this.currentStyle
    }
    if (window.getComputedStyle) {
        return window.getComputedStyle(this, null)[prop]
    } else {
        return this.currentStyle[prop];
    }
}

/**
 * : RGB16
 */
String.prototype.colorHex = function () {
    // RGB
    var reg = /^(rgb|RGB)/;
    var color = this;
    if (reg.test(color)) {
        var strHex = "#";
        // RGB3
        var colorArr = color.replace(/(?:\(|\)|rgb|RGB)*/g, "").split(",");
        // 16
        for (var i = 0; i < colorArr.length; i++) {
            var hex = Number(colorArr[i]).toString(16);
            if (hex === "0") {
                hex += hex;
            }
            strHex += hex;
        }
        return strHex;
    } else {
        return String(color);
    }
};

/**
 * : 16RGB
 */
String.prototype.colorRgb = function () {
    // 16
    var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
    // 
    var color = this.toLowerCase();
    if (reg.test(color)) {
        // #fff => #ffffff
        if (color.length === 4) {
            var colorNew = "#";
            for (var i = 1; i < 4; i += 1) {
                colorNew += color.slice(i, i + 1).concat(color.slice(i, i + 1));
            }
            color = colorNew;
        }
        // RGB
        var colorChange = [];
        for (var i = 1; i < 7; i += 2) {
            colorChange.push(parseInt("0x" + color.slice(i, i + 2)));
        }
        return "RGB(" + colorChange.join(",") + ")";
    } else {
        return color;
    }
};
// 
String.prototype.replaceAll = function (s1, s2) {
    return this.replace(new RegExp(s1, "gmi"), s2);
}
/**
 * :
 * format:yyyy/MM/dd
 * 
 * */
// 
Date.prototype.format = function (format) {
    var args = {
        "M+": this.getMonth() + 1,
        "d+": this.getDate(),
        "h+": this.getHours(),
        "m+": this.getMinutes(),
        "s+": this.getSeconds(),
        "q+": Math.floor((this.getMonth() + 3) / 3), //quarter
        "S": this.getMilliseconds()
    };
    if (/(y+)/.test(format)) format = format.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var i in args) {
        var n = args[i];
        if (new RegExp("(" + i + ")").test(format)) format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? n : ("00" + n).substr(("" + n).length));
    }
    return format;
};

//
export function getRandomNumber(n) {
    var str = "";
    for (var i = 0; i < n; i++) {
        var dic = Math.random() + "";
        str += dic.charAt(3);
    }
    return str;
};

// ID
const getRandomUniqueID = function () {
    return new Date().getTime() + getRandomNumber(5)
}

const os = (function() {
    let UserAgent = navigator.userAgent.toLowerCase();
    return {
        isIpad          : /ipad/.test(UserAgent),
        isIphone        : /iphone os/.test(UserAgent),
        isAndroid       : /android/.test(UserAgent),
        isWindowsCe     : /windows ce/.test(UserAgent),
        isWindowsMobile : /windows mobile/.test(UserAgent),
        isWin2K         : /windows nt 5.0/.test(UserAgent),
        isXP            : /windows nt 5.1/.test(UserAgent),
        isVista         : /windows nt 6.0/.test(UserAgent),
        isWin7          : /windows nt 6.1/.test(UserAgent),
        isWin8          : /windows nt 6.2/.test(UserAgent),
        isWin81         : /windows nt 6.3/.test(UserAgent),        isMac           : /mac os/.test(UserAgent)
    };
}());

const bw = (function() {
    let UserAgent = navigator.userAgent.toLowerCase();
    return {
        isUc      : /ucweb/.test(UserAgent), // UC
        isChrome  : /chrome/.test(UserAgent), // Chrome
        isFirefox : /firefox/.test(UserAgent), // 
        isOpera   : /opera/.test(UserAgent),  // Opera
        isSafire  : /safari/.test(UserAgent) && !/chrome/.test(UserAgent), // safire
        is360     : /360se/.test(UserAgent), // 360
        isBaidu   : /bidubrowser/.test(UserAgent), // 
        isSougou  : /metasr/.test(UserAgent), // 
        isIE6     : /msie 6.0/.test(UserAgent), // IE6
        isIE7     : /msie 7.0/.test(UserAgent), // IE7
        isIE8     : /msie 8.0/.test(UserAgent), // IE8
        isIE9     : /msie 9.0/.test(UserAgent), // IE9
        isIE10    : /msie 10.0/.test(UserAgent), // IE10
        isIE11    : /msie 11.0/.test(UserAgent), // IE11
        isLB      : /lbbrowser/.test(UserAgent), // 
 isWX      : /micromessenger/.test(UserAgent), // 
        isQQ      : /qqbrowser/.test(UserAgent) // QQ
    };
}());

export default {
    changeColorTheme,
    getAllParents,
    isHasInParent,
    getRandomNumber,
    getRandomUniqueID,
    os,
    bw
}